.table {
  display: flex;
  overflow: hidden;

  @media (min-width: @tablet) {
    width: 620px;
    margin: 0 auto;
  }

  @media (min-width: @desktop) {
    width: 940px;
    margin: 0 auto;
  }
}

.table__column {
  list-style: none;
  flex-grow: 1;

  &--mobile-off {
    @media (max-width: @mobile) {
      display: none;
    }
  }
}

.table__cell {
  background-color: var(--basic-white);
  border: 1px solid var(--border-gray);
  border-radius: 2px;
}

.table__header {
  background-color: var(--basic-gray);

  @media (max-width: @mobile) {
    width: 280px;
  }

  @media (min-width: @tablet) {
    height: 75px;
  }

  @media (min-width: @desktop) {
    height: 94px;
  }

  &--empty {
    background-color: transparent;
    border-top: none;
    border-left: none;
  }
}

.table__heading {
  color: var(--basic-white);
  font-size: 16px;
  line-height: 1.87em;
  font-weight: 700;
  text-transform: uppercase;
}

.table__pricing {
  color: var(--basic-white);
  font-size: 18px;
  line-height: 1.67em;
  font-weight: 300;
}

.table__features {
  font-size: 14px;
  line-height: 2.14em;
  color: var(--basic-gray);
  text-transform: uppercase;
}
